import { mapGetters, mapState } from 'vuex';
export default {
    name: "second-class",

    props: ['list', `activeIndexTwo`, `theme`],

    computed: {
        ...mapState({
            catsImg: state => state.mallConfig.__wxapp_img.cats,
        })
    },
    data() {
        return {
            switchBool: false
        }
    },

    methods: {
        setNav(it, key) {
            this.switchBool = false;
            this.$emit('setNav',{
                it, key
            });
        }
    }
}
